.image-block {
   width: 100%;
   max-width: 350px;
   height: 300px;
   display: flex;
   justify-content: center;
   align-items: center;
   border-radius: $border-radius;
   background-color: $orange;
   img {
      width: 100%;
      height: 100%;
      object-fit: contain;
   }
}
